    // 添加到总部下
    generateTree: function generateTree() {
      var pid = this.sourceList[0].GroupID;
      for (var i = 1; i < this.sourceList.length; i++) {
        if (this.sourceList[i].ParentID === pid) {
          var item = {
            GroupID: this.sourceList[i].GroupID,
            Name: this.sourceList[i].Name,
            // Children: [],
            Roles: this.sourceList[i].Roles,
            Level: this.sourceList[i].Level
          };
          this.tree.push(item);
          this.addChildren(item, i);
        }
      }
    },

    // 递归排列职位
    addChildren: function addChildren(item, currentIndex) {
      for (var i = currentIndex + 1; i < this.sourceList.length; i++) {
        if (this.sourceList[i].ParentID === item.GroupID) {
          var child = {
            GroupID: this.sourceList[i].GroupID,
            Name: this.sourceList[i].Name,
            // Children: [],
            Roles: this.sourceList[i].Roles,
            Level: this.sourceList[i].Level
          };
          if (!item.Children) {
            item.Children = [];
          }
          item.Children.push(child);
          this.addChildren(child, i);
        }
      }
    },

