<template>
<section v-loading.fullscreen.lock='isloading'>
  <el-header class = 'header' style="background:#EDF2FC;">
   <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item style="line-height:40px;">人事管理</el-breadcrumb-item>
    </el-breadcrumb>
 </el-header>
 <el-main>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="searchCriteriaForm" size='mini' class="el-form-headers">
        <el-row class="toolbarRows" :style='"padding-bottom:" + paddingRow + "px;"'>
				<el-form-item style="width:120px;">
					<el-input v-model="searchCriteriaForm.keyword" placeholder="关键字"></el-input>
				</el-form-item>
         <el-form-item  prop='selectedOptions' style="width:150px;">
        <el-cascader
          placeholder="选择部门"
          :options="dataList"
          :props="defaultProps"
          :clearable = 'true'
          change-on-select
          v-model="searchCriteriaForm.groupIDPath"
          filterable
          @change="handGroupIDChange"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item style="width:150px;">
            <el-select v-model="searchCriteriaFormBefore.roleID"  placeholder="选择职位" :clearable = 'true'>
            <el-option
              v-for="item in roleIDForm"
              :key="item.RoleID"
              :label="item.Name"
              :value="item.RoleID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width:120px;">
          <el-select v-model="searchCriteriaFormBefore.StaffStatus"  placeholder="选择在职状况" :clearable = 'true'>
            <el-option
              v-for="item in staffStatusList"
              :key="item.StaffStatusID"
              :label="item.Name"
              :value="item.StaffStatusID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
					<el-button plain v-on:click="getMore" :icon="iconPath"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsersed">查询</el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="primary" v-on:click="getUsersAll">全部</el-button>
				</el-form-item>
				<el-form-item>
					<el-button class="filter-item" @click="handleCreate" type="primary" icon="edit">新增</el-button>
				</el-form-item>
        <!-- </el-col> -->
        </el-row>
        <!-- <el-row> -->
      <el-row class="toolbarRow" style="padding-bottom: 0px;" v-if="isShowRow">
         <el-form-item>
           <el-form-item style="width:120px;">
            <el-select v-model="searchCriteriaForm.IsInBlackList"  placeholder="黑名单" :clearable = 'true'>
            <el-option label="是" value="true"> </el-option>
            <el-option label="否" value="false"> </el-option>
          </el-select>
        </el-form-item>
         <el-form-item style="width:150px;">
            <el-select v-model="searchCriteriaForm.IsViaReferral"  placeholder="转介绍入职" :clearable = 'true'>
            <el-option label="是" value="true"> </el-option>
            <el-option label="否" value="false"> </el-option>
          </el-select>
        </el-form-item>
        </el-form-item>
         </el-row>
      <!-- </el-col> -->
			</el-form>
		</el-col>
    <el-col v-if="rowClickListShow" style="padding:0 0 5px 0;">
      <!-- <template :slot-scope="rowClickList"> -->
        <el-button-group :slot-scope="rowClickList">
        <el-button  @click="handleEdit(rowClickList)" size = "mini">编辑</el-button>
        <el-button  @click="getDetails(rowClickList)" v-if="rowClickList.showStaff" size = "mini">入职</el-button>
        <el-button  @click="getExitUser(rowClickList)" v-if="rowClickList.showExit" size = "mini">离职</el-button> 
        <el-button  @click="getInBlack(rowClickList)" size = "mini" v-if = "(rowClickList.StaffStatus.StaffStatusID !== 2)">{{rowClickList.IsInBlackList === false ? '加入黑名单':'取消黑名单'}}</el-button>
        <template v-if="!rowClickList.IsInBlackList">
        <el-button  @click="turnoverClick(rowClickList)" size = "mini">部门调整</el-button>
        <el-button  @click="changeRiderClick(rowClickList)" v-if="rowClickList.RiderJobType.Name" size = "mini">骑手职位调整</el-button>
        <el-button  @click="editRiderClick(rowClickList)" size = "mini">招聘渠道</el-button>
        <el-button  @click="protocolClick(rowClickList)" size = "mini">员工协议</el-button>
        <el-button  @click="InsuranceClick(rowClickList)" size = "mini">保险信息</el-button>
        <el-button  @click="bankClick(rowClickList)" size = "mini">银行信息</el-button>
        <el-button  @click="riderEleClick(rowClickList)" size = "mini">饿了么ID</el-button>
        </template>
        </el-button-group>
			<!-- </template> -->
    </el-col>
    <!-- </el-row> -->
		<!--列表-->
		<el-table :data="users" highlight-current-row style="width: 100%;" border cell-class-name='table_style' @row-click='rowRow' size='mini'>
      <el-table-column prop="StaffID" label='#' width="60" fixed="left">
      </el-table-column>
			 <el-table-column prop="Name" label="姓名" width="80" fixed="left" sortable>
			 </el-table-column>
			<el-table-column prop='StaffStatus.Name' label="在职状态" width="70">
			</el-table-column>
			<el-table-column label="部门" width="150">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.Groups.name == null?'-': scope.row.Groups.name}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="黑名单" width="60">
         <template slot-scope="scope">
          <p size="mini" class="p_Style">{{scope.row.IsInBlackList ? '&radic;':''}}</p> 
				</template>
			</el-table-column>
			<el-table-column label="职位" width="120">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.Role == null?'': scope.row.Role.name}}</p> 
				</template>
			</el-table-column>
      <el-table-column prop="RiderJobType.Name" label="职位类型" width="100">
        <template slot-scope="scope">
          <P size="mini">{{scope.row.RiderJobType.Name}}</p>
        </template>
			</el-table-column>
      <el-table-column prop="EditRiderEleID" label="饿了么ID" width="130">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.RiderEleID == null?'': scope.row.RiderEleID}}</p> 
				</template> 
			</el-table-column> 
      <el-table-column label="最后跑单时间" width="120">
			</el-table-column>
      	<el-table-column prop="SexText" label="性别" width="60">
			</el-table-column>
      	<el-table-column prop="Age" label="年龄" width="60">
			</el-table-column>
      <!-- <el-table-column prop="WorkNO" label="工号" width="200">
         <template slot-scope="scope">
          <p size="small" @click="editRiderClick(scope.row)">{{scope.row.WorkNO == null?'-': scope.row.WorkNO}}</p> 
				</template> 
			</el-table-column> -->
       <el-table-column  label="招聘渠道" width="100">
          <template slot-scope="scope">
          <p size="mini">{{scope.row.RecruitChannel.Name == null?'': scope.row.RecruitChannel.Name}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="介绍人" width="80">
          <template slot-scope="scope">
          <p size="mini">{{scope.row.RiderReferrerStaffID == null?'': scope.row.RiderReferrerStaffID}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="介绍日期" width="100">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.RiderReferrerDate == null?'': scope.row.RiderReferrerDate}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="介绍附件" width="100">
         <template slot-scope="scope">
          <!-- <p size="mini" @click="clickChange(scope)">点击</p> -->
           <!-- <p size="mini">{{scope.row.RiderReferrerAttachmentURL == null?'': scope.row.RiderReferrerAttachmentURL}}</p>  -->
            <span size="mini" @click="clickChange(scope.row)" v-if='scope.row.RiderReferrerAttachmentURL'>详情</span>
				</template>
			</el-table-column>
      <el-table-column label="用工性质" width="100">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.JobNature.Name == null?'':scope.row.JobNature.Name}}</p> 
				</template>
			</el-table-column>
      <el-table-column label="协议编号" width="150">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.ProtocolNO == null?'':scope.row.ProtocolNO}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="合同期次" width="70">
          <template slot-scope="scope">
          <p size="mini">{{scope.row.ProtocolTime.Name == null?'':scope.row.ProtocolTime.Name}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="协议类型" width="100">
          <template slot-scope="scope">
          <p size="mini">{{scope.row.ProtocolType.Name == null?'':scope.row.ProtocolType.Name}}</p> 
				</template>
			</el-table-column>
      <el-table-column prop='ProtocolSignedDate' label="签署日期" width="100" sortable>
         <!-- <template slot-scope="scope">
          <p size="mini">{{scope.row.ProtocolSignedDate == null?'':scope.row.ProtocolSignedDate}}</p> 
				</template> -->
			</el-table-column>
       <el-table-column label="协议期限" width="200">
          <template slot-scope="scope">
          <p size="mini">{{scope.row.ProtocolBeginDate == null?'':scope.row.ProtocolBeginDate + (scope.row.ProtocolEndDate == null ? '' :'/') + (scope.row.ProtocolEndDate == null ? '' :scope.row.ProtocolEndDate)}}</p> 
				</template>
			</el-table-column>
			</el-table-column>
      <el-table-column label="保险性质" width="100">
         <template slot-scope="scope">
          <p size="mini">{{scope.row.InsuranceNature.Name == null?'':scope.row.InsuranceNature.Name}}</p> 
				</template>
			</el-table-column>
      <el-table-column label="保险期限" width="200">
        <template slot-scope="scope">
          <p size="mini">{{scope.row.InsuranceStartBuyDate  == null?'':scope.row.InsuranceStartBuyDate + (scope.row.InsuranceStopBuyDate == null ? '' : '/') + scope.row.InsuranceStopBuyDate}}</p> 
				</template>
			</el-table-column>
      <el-table-column label="开户行" width="80">
        <template slot-scope="scope">
          <p size="mini">{{scope.row.BankType.Name == null?'':scope.row.BankType.Name}}</p> 
				</template>
			</el-table-column>
       <el-table-column label="银行卡号" width="200">
          <template slot-scope="scope">
          <p size="mini" class="p_Style">{{scope.row.BankNO == null?'':scope.row.BankNO}}</p> 
				</template>
			</el-table-column>
			</el-table-column>
      	<el-table-column prop="Birthday" label="出生日期" width="120">
			</el-table-column>
      	<el-table-column prop="StaffMobile" label="手机号码" width="120">
			</el-table-column>
      	<el-table-column prop="MaritalStatusText" label="婚姻" width="60">
			</el-table-column>
			<el-table-column prop="IDCardNO" label="身份证号码" width="180">
			</el-table-column>
      <el-table-column prop="School" label="毕业学校" width="150">
			</el-table-column>
      <el-table-column prop="Major" label="专业" width="150">
			</el-table-column>
      <el-table-column prop="Education.Name" label="学历" width="60">
			</el-table-column>
      <el-table-column label="入职备注" width="80">
       <template slot-scope="scope">
        <el-tooltip class="item" effect="dark" :content="scope.row.EntryRemark" placement="top">
         <p>{{scope.row.EntryRemark ==null?'':'详情'}}</p>
        </el-tooltip>
       </template> 
			</el-table-column>
      <el-table-column prop="Education.Name" label="离职备注" width="80">
        <template slot-scope="scope">
        <el-tooltip class="item" effect="dark" :content="scope.row.ExitRemark" placement="top">
         <p>{{scope.row.ExitRemark ==null?'':'详情'}}</p>
        </el-tooltip>
       </template> 
			</el-table-column>
      <el-table-column prop="Household" label="户籍" width="200">
			</el-table-column>
      <el-table-column prop="Residence" label="居住地" width="200">
			</el-table-column>
      <el-table-column prop="EmergencyContact" label="紧急联系人" width="100">
			</el-table-column>
      <el-table-column prop="EmergencyContactRelationship" label="关系" width="60">
			</el-table-column>
			<el-table-column prop="EmergencyContactMobile" label="联系人手机号" min-width="130">
			</el-table-column> 
			<!-- <el-table-column label="操作" width="250" fixed="right"> -->
				<!-- <template slot-scope="scope">
          <el-button  @click="getDetails(scope.row)" v-if="scope.row.showStaff" size = "mini">入职</el-button>
          <el-button  @click="getExitUser(scope.row)" v-if="scope.row.showExit" size = "mini">离职</el-button> 
					<el-button  @click="handleEdit(scope.row)" size = "mini">编辑</el-button>
          <el-button  @click="getInBlack(scope.row)" v-if="scope.row.showInBlack" size = "mini">黑名单</el-button> 
				</template> -->
			<!-- </el-table-column> -->
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
		<el-pagination
      @size-change="handlePaginationSizeChange"
      @current-change="handlePaginationCurrentChange"
      :current-page="pagingInfoForm.pageNumber"
      :page-sizes="[20, 50, 100, 200, 400]"
      :page-size="pagingInfoForm.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.totalItemCount"
      v-if="page.totalItemCount"
      style="float:right;"
      >
    </el-pagination>
		</el-col>

 <!-- 断点 -->
<el-dialog :title="textMap[dialogStatus]" :close-on-click-modal="false" :visible.sync="dialogFormVisible" class="el_dialogs">
<el-form class="small-space" :model="mainForm" ref="mainForm" :rules='rolus' label-position="right" label-width="160px" size="mini" style='width: 500px; margin-left:50px;'>
  <el-tabs type="border-card" >
   <!-- from -->
  <el-tab-pane label="员工信息">
        <el-form-item label="姓名" prop="Name">
          <el-input v-model="mainForm.Name"></el-input>
        </el-form-item>
          <el-form-item label="部门" prop='selectedOptions'>
        <el-cascader
          :placeholder="dataText"
          :options="dataList"
          :props="defaultProps"
          :disabled='disabledStyle'
          v-model="mainForm.selectedOptions"
          filterable
          @change="handleItemChange"
          >
          </el-cascader>
        </el-form-item>

        <el-form-item label="职位" prop='RoleID'>
         <el-select v-model="mainForm.RoleID"  placeholder="请选择" :disabled='disabledStyle' >
         <el-option
          v-for="item in options"
          :key="item.RoleID"
          :label="item.Name"
          :value="item.RoleID">
        </el-option>
      </el-select>
        </el-form-item>
        <el-form-item label="骑手职位类型" v-if="isShowRider">
         <el-select v-model="mainForm.RiderJobTypeID"  placeholder="请选择" :disabled='disabledStyle'>
         <el-option
          v-for="item in RiderJobOptions"
          :key="item.RiderJobTypeID"
          :label="item.Name"
          :value="item.RiderJobTypeID">
        </el-option>
      </el-select>
        </el-form-item>
      <el-form-item label="性别">
			 <el-radio-group v-model="mainForm.Sex">
				<el-radio label="1">男</el-radio>
				<el-radio label="2">女</el-radio>
			</el-radio-group>
      </el-form-item>
        <el-form-item label="身份证号" prop="IDCardNO">
          <el-input v-model="mainForm.IDCardNO" @blur="getBirthdatByIdNo(mainForm.IDCardNO)"></el-input>
        </el-form-item>
        <el-form-item label="出生日期" >
          <el-date-picker v-model="mainForm.Birthday" type="date" placeholder="选择出生日期" format="yyyy 年 MM 月 dd 日"
      value-format="yyyy-MM-dd" :disabled='true'>
        </el-date-picker>
        </el-form-item>
        <el-form-item label="年龄" prop="Age">
          <el-input v-model.number="mainForm.Age" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="StaffMobile">
          <el-input v-model="mainForm.StaffMobile"></el-input>
        </el-form-item>
      <el-form-item label="婚姻状况">
			 <el-radio-group v-model="mainForm.MaritalStatus">
				<el-radio label="1">未婚</el-radio>
				<el-radio label="2">已婚</el-radio>
        <el-radio label="3">离异</el-radio>
			 </el-radio-group>
       </el-form-item>
        <el-form-item label="毕业学校">
          <el-input v-model="mainForm.School"></el-input>
        </el-form-item>
         <el-form-item label="专业">
          <el-input v-model="mainForm.Major"></el-input>
        </el-form-item>
         <el-form-item label="学历">
         <el-select v-model="mainForm.EducationID" placeholder="请选择" :clearable ='isclearable'>
         <el-option
          v-for="item in EducationOptions"
          :key="item.EducationID"
          :label="item.Name"
          :value="item.EducationID">
        </el-option>
      </el-select>
        </el-form-item>
         <el-form-item label="户籍">
          <el-input v-model="mainForm.Household"></el-input>
        </el-form-item>
         <el-form-item label="居住地">
          <el-input v-model="mainForm.Residence"></el-input>
        </el-form-item>
         <el-form-item label="紧急联系人">
          <el-input v-model="mainForm.EmergencyContact"></el-input>
        </el-form-item>
        <el-form-item label="与本人关系">
          <el-input v-model="mainForm.EmergencyContactRelationship"></el-input>
        </el-form-item>
        <el-form-item label="紧急联系人手机号码"  prop="EmergencyContactMobile">
          <el-input v-model="mainForm.EmergencyContactMobile"></el-input>
        </el-form-item>
  </el-tab-pane>
  <el-tab-pane>
    <span slot="label"><i class="el-icon-date"></i> 账户信息</span>
        <el-form-item label="登录名"  prop="Username" :rules="rolus.item">
          <el-input v-model="mainForm.Username"></el-input>
        </el-form-item>
        <el-form-item label="员工状态" :required='true'>
			 <el-radio-group v-model="mainForm.Status">
				<el-radio label="NotSet">未设</el-radio>
				<el-radio label="Normal">默认</el-radio>
        <el-radio label="PendingApproval">待审</el-radio>
				<el-radio label="Removed">删除</el-radio>
			</el-radio-group>
      </el-form-item>
      <el-form-item label="登录密码"  prop="Password">
          <el-input v-model="mainForm.Password" type='password'></el-input>
        </el-form-item>
        <el-form-item label="再次输入登录密码"  prop="PasswordConfirm">
          <el-input v-model="mainForm.PasswordConfirm" type='password'></el-input>
        </el-form-item>
       <el-form-item label="昵称">
          <el-input v-model="mainForm.DisplayName" type='text'></el-input>
        </el-form-item>
         <el-form-item label="真实名称">
          <el-input v-model="mainForm.RealName" type='text'></el-input>
        </el-form-item>
      <el-form-item label="真实名称是否验证">
      <el-switch v-model="mainForm.RealNameIsValid"></el-switch>
     </el-form-item>
       <el-form-item label="邮箱"  prop="Email">
          <el-input v-model="mainForm.Email" type='text'></el-input>
        </el-form-item>
      <el-form-item label="邮箱是否验证">
      <el-switch v-model="mainForm.EmailIsValid"></el-switch>
     </el-form-item>
       <el-form-item label="手机号码"  prop="Mobile">
          <el-input v-model="mainForm.Mobile" type='text'></el-input>
        </el-form-item>
      <el-form-item label="手机号码是否验证">
      <el-switch v-model="mainForm.MobileIsValid"></el-switch>
     </el-form-item>
     <el-form-item label="描述">
          <el-input v-model="mainForm.Description" type='text'></el-input>
    </el-form-item>
   </el-tab-pane>
  </el-tabs>
 </el-form>
    <div  class="submit"> 
      <div class="buttonStyle">
        <el-button @click="dialogFormVisible = false" class="userdButton">取 消</el-button>
        <el-button v-if="dialogStatus=='create'" type="primary" @click="create('mainForm')" class="userdButton">确 定</el-button>
        <el-button v-else type="primary" @click="update">确 定</el-button>
     </div>   
    </div> 
    </el-dialog>

    <!-- 员工入职 -->
  <el-dialog :title="userEntry" :visible.sync="isEntry" :close-on-click-modal="false" class="entry_style">
  <el-form class="small-space" :model="entryForm" ref="entryForm" :rules='rolus' label-position="right" label-width="160px" size="mini" style='width: 500px; margin-left:50px;'>
  <el-tabs type="border-card">
   <!-- from -->
  <el-tab-pane label="入职信息">
      <el-form-item label="部门" prop='selectedOptions'>
        <el-cascader
          :placeholder="dataText"
          :options="dataList"
          :props="defaultProps"
          v-model="entryForm.selectedOptions"
          filterable
          @change="handleItemChange"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="职位" prop='RoleID'>
         <el-select v-model="entryForm.RoleID"  placeholder="请选择" >
         <el-option
          v-for="item in options"
          :key="item.RoleID"
          :label="item.Name"
          :value="item.RoleID">
        </el-option>
      </el-select>
        </el-form-item>
        <el-form-item label="骑手职位类型" v-if="isShowRider">
         <el-select v-model="entryForm.RiderJobTypeID"  placeholder="请选择" >
         <el-option
          v-for="item in RiderJobOptions"
          :key="item.RiderJobTypeID"
          :label="item.Name"
          :value="item.RiderJobTypeID">
