      // 获取在职情况
      getStaffStatus () {
        api.getStaffStatus().then(response => {
          this.staffStatusList = response.data.list
          // console.log('在职状况' + JSON.stringify(response))
        }, error => {
          this.showErrorMessage(error.message)
        })
      },
      // 获取职位
      getRole () {
        api.getRole().then(response => {
          this.roleIDForm = response.data.list
          // console.log('职位' + JSON.stringify(response.data.list))
        }, error => {
          this.showErrorMessage(error.message)
        })
      },
      handleCreate () {
        this.disabledStyle = false
        this.dialogStatus = 'create'
        this.dialogFormVisible = true
        this.resetTemp()
        this.randomWord(false, 6)
        this.mainForm.Password = '111111'
        this.mainForm.PasswordConfirm = '111111'
        this.dataText = '试试搜索'
        this.isEntryForm = false
      },
      // getStaffInfoList () {
      //   var _this = this
