      //     })
      //   })
      // },
      trim (s) {
        return s.replace(/^\s+|\s+$/g, '')
      },
      // gettree
      getTree () {
        api.getGroupTree().then(response => {
          this.treeData = response.data.tree
        }, error => {
          this.showErrorMessage(error.message)
        })
      },
      // 验证身份证号并获取出生日期
      getBirthdatByIdNo (iIdNo) {
        // console.log('身份证', iIdNo)
        var tmpStr = ''
        // var idDate = ''
        // var tmpInt = 0
        // var strReturn = ''
        iIdNo = this.trim(iIdNo)
        // if ((iIdNo.length !== 15) && (iIdNo.length !== 18)) {
        //   strReturn = '输入的身份证号位数错误'
        //   return strReturn
        // }
        if (iIdNo.length === 15) {
          tmpStr = iIdNo.substring(6, 12)
          tmpStr = '19' + tmpStr
          tmpStr = tmpStr.substring(0, 4) + '-' + tmpStr.substring(4, 6) + '-' + tmpStr.substring(6)
          this.mainForm.Birthday = tmpStr
          const data = new Date()
          const year = parseInt(data.getFullYear())
          const oldYear = parseInt(tmpStr.substring(0, 4))
          this.mainForm.Age = year - oldYear
          // console.log('15位日期', year - oldYear)
        } else {
          tmpStr = iIdNo.substring(6, 14)
          tmpStr = tmpStr.substring(0, 4) + '-' + tmpStr.substring(4, 6) + '-' + tmpStr.substring(6)
          this.mainForm.Birthday = tmpStr
          const data = new Date()
          const year = parseInt(data.getFullYear())
          const oldYear = parseInt(tmpStr.substring(0, 4))
          this.mainForm.Age = year - oldYear
          // console.log('18位日期', year - oldYear)
        }
      },
      // 提交资料
      handleChangeHeadURLBrowser () {
        this.isUploadEntryForm = false
        this.popupCKFinder('RiderReferrerAttachmentURL')
      },
      handleChangeHeadURL () {
        this.isUploadEntryForm = true
        this.popupCKFinder('RiderReferrerAttachmentURL')
      },
      // popupCKFinder
      popupCKFinder (name) {
        const _this = this
      /* eslint-disable no-undef */
        try {
          CKFinder.popup({
            chooseFiles: true,
            width: 800,
            height: 600,
            onInit: function (finder) {
              finder.on('files:choose', function (evt) {
                const file = evt.data.files.first()
                if (!this.isUploadEntryForm) {
                  _this.editRiderForm[name] = file.getUrl()
                } else {
                  _this.entryForm[name] = file.getUrl()
                }
              })
              finder.on('file:choose:resizedImage', function (evt) {
                if (!this.isUploadEntryForm) {
                  _this.editRiderForm[name] = evt.data.resizedUrl
                } else {
                  _this.entryForm[name] = evt.data.resizedUrl
                }
              })
            }
          })
        } catch (e) {
          // console.log(e.message)
          this.showErrorMessage(e.message)
        }
      },
      // 反向查询
      getGroupIDPath (tree, id) {
        if (!tree) return
        // console.log('我是大数据' + JSON.stringify(this.treeData))
        for (let node of tree) {
          if (node.id === id) {
            // console.log('我是大数据' + j + JSON.stringify(node.parent))
            this.turnoverForm.selectedOptions = node.parentIDPath ? node.parentIDPath.concat() : []
            this.turnoverForm.selectedOptions.push(id)
            break
          } else {
            this.getGroupIDPath(node.children, id)
          }
        }
        // console.log('调整数据', JSON.stringify(list))
      },
      // 员工部门调整
      turnoverClick (scope) {
        this.resetTurnover()
        this.dataText = '请选择'
        this.isTurnover = true
        this.userEntry = scope.Name + ' 部门调整'
        this.turnoverForm.StaffID = scope.StaffID
        // this.turnoverForm.selectedOptions = [scope.Groups.groupID]
        this.turnoverForm.GroupID = scope.Groups.groupID
        this.getGroupIDPath(this.treeData, scope.Groups.groupID)
        this.handleItemChange([scope.Groups.groupID])
        this.turnoverForm.RoleID = scope.Role.roleID
        var _this = this
        this.$nextTick(() => {
          _this.$refs['turnoverForm'].clearValidate()
        })
        // console.log('部门调整' + JSON.stringify(scope))
      },
      // 关闭部门调整界面
      turnoverCancel () {
        this.isTurnover = false
        this.resetTurnover()
      },
      // 提交部门调整数据
      turnoverCreate (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // console.log('hhhhhh')
            const newturnForm = {
              StaffID: this.turnoverForm.StaffID,
              StaffTurnoverTypeID: this.turnoverForm.StaffTurnoverTypeID,
              GroupID: this.turnoverForm.GroupID,
              RoleID: this.turnoverForm.RoleID,
              RequestRemark: this.turnoverForm.RequestRemark
            }
            this.isloading = true
            // console.log('提交部门数据', newturnForm)
            api.getTurnoverList(newturnForm).then(response => {
              this.showSuccessMessage(response.data.message)
              this.isloading = false
              this.isTurnover = false
              this.getStaffBaseList()
              this.resetTurnover()
            }, error => {
              this.showErrorMessage(error.message)
              this.isloading = false
            })
          } else {
            this.showErrorMessage('请提交正确的部门数据')
          }
        })
      },
      // 清楚部门类型数据
      resetTurnover () {
        this.turnoverForm = {
          StaffID: '',
          StaffTurnoverTypeID: '',
          GroupID: '',
          RoleID: '',
          RequestRemark: ''
        }
      },
