      changeRiderClick (scope) {
        this.resetchangeRider()
        this.isRiderJobType = true
        this.userEntry = scope.Name + ' 骑手职位类型调整'
        this.changeRiderJobTypeForm.RiderJobTypeID = scope.RiderJobType.RiderJobTypeID
        this.changeRiderJobTypeForm.StaffID = scope.StaffID
