        this.isRiderEle = true
        this.isloading = false
        this.riderEleForm = {
          StaffID: '',
          RiderEleID: ''
        }
        this.userEntry = scope.Name + ' 补充饿么ID'
        this.riderEleForm.StaffID = scope.StaffID
        this.riderEleForm.RiderEleID = scope.RiderEleID
        var _this = this
        this.$nextTick(() => {
          _this.$refs['riderEleForm'].clearValidate()
        })
      },
