              this.showSuccessMessage(response.data.message)
              this.getStaffBaseList()
              this.isloading = false
              this.isRiderEle = false
            }, error => {
              this.showErrorMessage(error.message)
              this.isloading = false
            })
            // console.log('hhhhhh' + JSON.stringify(setEleForm))
          } else {
            this.showErrorMessage('请提交正确的饿了么ID')
          }
        })
      },
      // 获取黑名单
      getInBlack (scope) {
        this.isInBlack = true
        this.isloading = false
        this.resetinBlack()
        this.userEntry = scope.Name
        if (!scope.IsInBlackList) {
          this.blacklist = scope.Name + '加入'
          this.inBlackForm.IsInBlackList = 'true'
        } else {
          this.blacklist = scope.Name + '取消'
          this.inBlackForm.IsInBlackList = 'false'
        }
        this.inBlackForm.StaffID = scope.StaffID
