            if (list[i].ProtocolEndDate != null) {
              const endIndex = list[i].ProtocolEndDate.indexOf(' ')
              const strEnd = list[i].ProtocolEndDate.substr(0, endIndex)
              list[i].ProtocolEndDate = strEnd
            }
            if (list[i].RiderReferrerDate != null) {
              const startIndex = list[i].RiderReferrerDate.indexOf(' ')
              const strStart = list[i].RiderReferrerDate.substr(0, startIndex)
              list[i].RiderReferrerDate = strStart
            }
            if (list[i].InsuranceStartBuyDate != null) {
