      clickChange (scope) {
        this.userEntry = scope.Name + ' 介绍附件'
        this.isReferrerAttach = true
        this.riderAttachmentURL = scope.RiderReferrerAttachmentURL == null ? '' : scope.RiderReferrerAttachmentURL
        // console.log('点击数据' + JSON.stringify(scope))
      },
      // 取消英文
      entryCancel () {
        this.resetEntry()
        this.isEntry = false
        this.isShowRecruit = false
      },
      getCheckedNodes () {
        console.log('ss')
      },
      update () {
        this.isloading = true
        const mainForm = this.mainForm
        mainForm.GroupID = this.GroupID
        mainForm.RoleID = this.RoleID
        api.SetEdit(mainForm).then(response => {
          if (response.data.code === 200) {
            this.showSuccessMessage(response.data.message)
          }
          this.getStaffBaseList()
          this.isloading = false
          this.resetTemp()
          this.dialogFormVisible = false
          // console.log('编辑' + JSON.stringify(response))
        }, error => {
          this.showErrorMessage(error.message)
          this.isloading = false
          mainForm.RoleID = this.RoleName
          // console.log('setEditError' + JSON.stringify(error))
        })
        // console.log('数据' + JSON.stringify(mainForm))
      },
      // 入职添加
      entryCreate (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const entryForm = this.entryForm
            this.isloading = true
            api.getEntry(entryForm).then(response => {
              if (response.data.code === 200) {
                this.showSuccessMessage(response.data.message)
              }
              this.getStaffBaseList()
              this.isloading = false
              this.isEntry = false
              this.isShowRecruit = false
              this.resetEntry()
              // console.log('入职' + JSON.stringify(response))
            }, error => {
              this.showErrorMessage(error.message)
              this.isloading = false
              // console.log('入职error' + JSON.stringify(error))
            })
            // console.log('hhhhhh' + JSON.stringify(entryForm))
          } else {
            this.showErrorMessage('请提交正确的入职数据')
          }
        })
      },
      resetEntry () {
        this.entryForm = {
          StaffID: '',
          EntryRemark: '',
          WorkNO: '',
          RecruitChannelID: '',
          RiderReferrerStaffID: '',
          RiderReferrerDate: '',
          RiderReferrerAttachmentURL: '',
          JobNatureID: '',
          ProtocolNO: '',
          ProtocolTimeID: '',
          ProtocolTypeID: '',
          ProtocolSignedDate: '',
          ProtocolBeginDate: '',
          InsuranceNatureID: '',
          InsuranceStartBuyDate: '',
          BankTypeID: '',
          BankNO: '',
          GroupID: '',
          RoleID: '',
          RiderJobTypeID: ''
        }
      },
      resetTemp () {
        this.cascaderText = []
        this.mainForm = {
          GroupID: '',
          RoleID: '',
          RiderJobTypeID: '',
          Username: '',
          Status: 'PendingApproval',
          Password: '',
          PasswordConfirm: '',
          DisplayName: '',
          RealName: '',
          RealNameIsValid: false,
          Email: '',
          EmailIsValid: false,
          Mobile: '',
          MobileIsValid: false,
          Description: '',
          Name: '',
          Age: '',
          Sex: '1',
          Birthday: '',
          StaffMobile: '',
          MaritalStatus: '',
          IDCardNO: '',
          EducationID: '',
          School: '',
          Major: '',
          Household: '',
          Residence: '',
          EmergencyContact: '',
          EmergencyContactRelationship: '',
          EmergencyContactMobile: ''
        }
        var _this = this
        this.$nextTick(() => {
          _this.$refs['mainForm'].clearValidate()
        })
      },
      // 清空搜索条件
      resetsearchCriteriaForm () {
        this.searchCriteriaForm = {
          keyword: '',
          groupIDs: [],
          roleIDs: [],
          groupIDPath: [],
          StaffStatusIDs: []
        }
      },
      // 查询
      getUsersed () {
        this.searchCriteriaForm.roleIDs = []
        this.searchCriteriaForm.StaffStatusIDs = []
        // console.log('hah', this.searchCriteriaFormBefore)
        // const roleID = []
        if (!this.searchCriteriaFormBefore.roleID) {
          this.searchCriteriaForm.roleIDs = []
        } else {
          this.searchCriteriaForm.roleIDs.push(this.searchCriteriaFormBefore.roleID)
        }
        if (!this.searchCriteriaFormBefore.StaffStatus) {
          this.searchCriteriaForm.StaffStatusIDs = []
        } else {
          this.searchCriteriaForm.StaffStatusIDs.push(parseInt(this.searchCriteriaFormBefore.StaffStatus))
        }
        // console.log('查询' + JSON.stringify(this.searchCriteriaForm))
        this.getStaffBaseList()
      },
      // 显示隐藏
      getMore () {
        this.isShowRow = !this.isShowRow
        if (!this.isShowRow) {
          this.iconPath = 'el-icon-caret-bottom'
          this.paddingRow = 12
        } else {
          this.paddingRow = 0
          this.iconPath = 'el-icon-caret-top'
        }
      },
      // 错误信息
      showErrorMessage (message) {
        this.$message({
          message: message,
          type: 'error'
        })
      },
      // 成功信息
      showSuccessMessage (message) {
        this.$message({
          message: message,
          type: 'success'
        })
      }
    }
  }
</script>
<style scodived lang="scss">
@import '../../styles/mixin.scss';
.el_dialogs>div{
  min-width:640px;
  width: 30%;
}
.dialog_style{
  min-width:1000px;
}
.toolbarRows .el-form-item {
  margin-bottom: 4px;
}
.toolbarRow .el-form-item {
   margin-bottom: 8px;
}
.el-form-item {
  margin-bottom: 5px;
}
.el-form-headers{
  margin-bottom: -12px;
}
.dropdown_style{
  position: relative;
  width: 192px;
}
.entry_styles>div{
  min-width:540px;
  width: 28%;
}
.entry_styled>div {
  min-width:800px;
  width: 35%;
}
.introduce_style>div{
  min-width:1000px;
  width: 30%;
}
.entry_style>div{
    min-width:600px;
    width: 30%;
}
.table_style{
  text-align: center;
}
.el-table th{
   text-align: center;
}
.el-table td{
  padding: 0;
}
.el-table .cell{
  line-height: normal;
}
.dropdown_children{
  width: 146px;
  height: 100%;
  margin-left: 15px;
}
.userdButton{
  margin-top:10px; 
}
.input_style{
  width: 200px;
}
p {
  -webkit-margin-before: 0.3em;
  -webkit-margin-after: 0.3em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
.optionStyle{
 height:300px;
 padding:0;
 background-color: #ffffff
}
.p_Style{
  width: 100%;
  height: 100%;
  line-height: 100%;
}
.el-dropdown-menu{
  min-width: 200px;
  max-height: 300px;
  padding: 10px 10px;
  overflow-y: scroll;
}
.el_icon{
  position: absolute;
  width: 30px;
  height:27px;
  top: 1px;
  right:2px;
  text-align: center;
  line-height: 27px;
  color: #b4bccc;
}
.buttonStyle{
  width: 200px;
  height:100%;
  position: absolute;
  top:0;
  left: 50%;
  margin-left: -100px;
}
.submit{
  position: relative;
  width: 100%;
  height: 50px;
}
.toolbars {
  padding: 5px 0;
}
.el-dialog__body{
 padding: 30px 20px 40px 20px; 
}
</style>



